import request from '@/utils/request'

//查询接口
export function queryAnalog(param){
    return request({
        url: '/analog/query',
        method: 'post',
        data: param || {}
    })
}

//新建接口
export function addAnalog(param){
    return request({
        url: '/analog/add',
        method: 'post',
        data: param || {}
    })
}

//更新接口
export function updateAnalog(param){
    return request({
        url: '/analog/update',
        method: 'put',
        data: param || {}
    })
}


