<template>


    <div>
        <h2>模拟接口</h2>
        <div>
            <el-form :inline="true" :model="form" class="demo-form-inline">
                <el-form-item label="接口名称:">
                    <el-input v-model="form.nameChina" placeholder="名称"></el-input>
                </el-form-item>
                <el-form-item label="接口路径:">
                    <el-input v-model="form.path" placeholder="路径"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
                <el-form-item>
                <el-button type="primary" @click="newInventory">新建</el-button>
                </el-form-item>
            </el-form>
        </div>
        
        
        <el-table v-loading="loading" :data="tableDate"> 

            <el-table-column label="id" prop='id' width="60">
                
            </el-table-column>
            <el-table-column label="名称" prop='nameChina'>
    
            </el-table-column>
            <el-table-column label="接口路径" prop='path'>
    
            </el-table-column>
            <el-table-column label="正常返回值" prop='returnCode'  :show-overflow-tooltip='true'>
                
            </el-table-column>
            <el-table-column label="当前状态" prop='status'  >
                <template slot-scope="scope">
                    <span>{{scope.row.status | macthStatue}}</span>
                </template>
            </el-table-column>
            <el-table-column label="创建人" prop='createUser'>
    
            </el-table-column>
            <el-table-column label="创建时间" prop='createDate'>
    
            </el-table-column>
            <el-table-column label="操作" width="300">
                <template slot-scope="scope" >
                   <sapn>
                        <el-button  type="primary" @click="changeStatue(scope.row)">更改状态</el-button>
                        <el-button type="success" @click="updateAna(scope.row)">修改接口</el-button>

                    </sapn> 
                </template>
                
            </el-table-column>
            <!-- <el-table-column label="修改记录" prop='pUnit'>
                <template slot-scope="scope" >
                   <sapn>
                        <el-button type="warning" @click="detail(scope.row)">详情</el-button>
                        

                    </sapn> 
                </template>
            </el-table-column> -->
        </el-table>
        <el-pagination
            :current-page.sync="currentPage"
            :page-size.sync="pageSize"
            :total="total"
            background
            @current-change="handleCurrentChange"
         />


        <div>
            
        </div>
        <!-- 新建接口 -->
        <div>
            <el-dialog title="新建接口" :visible.sync="loading1" width="30%" >
                
                <el-form :inline="true" :model="newForm" class="demo-form-inline">
                    <el-form-item label="中文名称:">
                        <el-input v-model="newForm.nameChina" ></el-input>
                    </el-form-item>
                    <el-form-item label="英文名称:">
                        <el-input v-model="newForm.nameEnglish" ></el-input>
                    </el-form-item>
                    <el-form-item label="接口路径:">
                        <el-input v-model="newForm.path" ></el-input>
                    </el-form-item>
                    <el-form-item label="正常返回值:">
                        <el-input v-model="newForm.returnCode" ></el-input>
                    </el-form-item>
                    <el-form-item label="异常一返回值:">
                        <el-input v-model="newForm.error1" ></el-input>
                    </el-form-item>
                     <el-form-item label="异常二返回值:">
                        <el-input v-model="newForm.error2" ></el-input>
                    </el-form-item>
                     <el-form-item label="异常三返回值:">
                        <el-input v-model="newForm.error3" ></el-input>
                    </el-form-item>
                   
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="loading1 = false">取 消</el-button>
                    <el-button type="primary" @click="onSubmitnewInven">确 定</el-button>
                </span>
                </el-dialog>
        </div>
        <!-- 修改接口状态弹窗 -->
        <div >
            <el-dialog title="修改接口状态" :visible.sync="loading2" width="30%" >
                <el-form :inline="true" :model="statueForm" class="demo-form-inline">
                    <div>
                    <el-form-item label="当前状态:" >
                        <span>{{statueForm.crrustatus}}</span>
                    </el-form-item>
                    </div>
                    <div>
                    <el-form-item label="修改状态:">
                        <el-select v-model="statueForm.status" placeholder="请选择">
                            <el-option
                                v-for="item in errorType"
                                :key="item.value"
                                :label="item.lable"
                                :value="item.value">
                            </el-option>
                         </el-select>
                    </el-form-item>
                    </div>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="loading2 = false">取 消</el-button>
                    <el-button type="primary" @click="onSubmitinputInven">确 定</el-button>
                </span>
            </el-dialog>

        </div>

         <!-- 修改弹窗 -->
        <div>
            <el-dialog title="修改接口" :visible.sync="loading3" width="30%" >
                <el-form :inline="true" :model="updateForm" class="demo-form-inline">
                    <el-form-item label="中文名称:">
                        <el-input v-model="updateForm.nameChina" ></el-input>
                    </el-form-item>
                    <el-form-item label="英文名称:">
                        <el-input v-model="updateForm.nameEnglish" ></el-input>
                    </el-form-item>
                    <el-form-item label="接口路径:">
                        <el-input v-model="updateForm.path" ></el-input>
                    </el-form-item>
                    <el-form-item label="正常返回值:">
                        <el-input v-model="updateForm.returnCode" ></el-input>
                    </el-form-item>
                    <el-form-item label="异常一返回值:">
                        <el-input v-model="updateForm.error1" ></el-input>
                    </el-form-item>
                     <el-form-item label="异常二返回值:">
                        <el-input v-model="updateForm.error2" ></el-input>
                    </el-form-item>
                     <el-form-item label="异常三返回值:">
                        <el-input v-model="updateForm.error3" ></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="loading3 = false">取 消</el-button>
                    <el-button type="primary" @click="onSubmitOutputInven">确 定</el-button>
                </span>
            </el-dialog>
        <!-- 库存记录详情 -->
        </div>
            <el-dialog title="库存记录" :visible.sync="loading5" width="75%" >
                
                <el-table v-loading="loading" :data="tableDate_invenChange"> 

                    <el-table-column label="序号" prop='id'>
                        
                    </el-table-column>
                    <el-table-column label="商品代码" prop='pCode'>
            
                    </el-table-column>
                    <el-table-column label="商品名称" prop='pName'>
            
                    </el-table-column>
                    <el-table-column label="操作" prop='pState' >
            
                    </el-table-column>
                    <el-table-column label="操作人" prop='pOperator' >
            
                    </el-table-column>
                    <el-table-column label="数量" prop='pNum'>
            
                    </el-table-column>
                    <el-table-column label="时间" prop='pDate'>
            
                    </el-table-column>
                    
                </el-table>
                <el-pagination
                    :current-page.sync="currentPage1"
                    :page-size.sync="pageSize1"
                    :total="total1"
                    background
                    @current-change="handleCurrentChange1"
                />

                <span slot="footer" class="dialog-footer">
                    <el-button @click="loading5 = false">取 消</el-button>
                    <el-button type="primary" @click="loading5 = false">确 定</el-button>
                </span>
            </el-dialog>
        <div>

        </div>
    </div>
    

</template>

<script>
//import { queryInventory, addInventory, updateInventory, addInvenChange, queryInvenChange } from '@/api/inventory'
//import { getCookie } from '@/utils/auth'
import { queryAnalog, addAnalog, updateAnalog } from '@/api/analog'
export default {

    name: 'InfoChangeTable',

    data() {
        return {

            //修改接口
            updateForm:{
                nameChina:'',
                nameEnglish:'',
                path:'',
                returnCode:'',
                error1:'',
                error2:'',
                error3:'',
                updateDate:'',
            },
            //异常种类
            errorType:[
                {lable:'正常',value:'0'},
                {lable:'异常一',value:'1'},
                {lable:'异常二',value:'2'},
                {lable:'异常三',value:'3'}
            ],
            //修改状态
            statueForm:{
                id:'',
                status:'',
                crrustatus:''
            },

            // 分页
            currentPage: 1,
            pageSize:8,
            total: 0,
             // 分页1
            currentPage1: 1,
            pageSize1:8,
            total1: 0,
            form:{
                path:'',
                nameChina:''
            },
            //新增商品 表单
            newForm:{
                nameChina:'',
                nameEnglish:'',
                path:'',
                returnCode:'',
                error1:'',
                error2:'',
                error3:'',
                createDate:'',
            },
            //入库表单
            inputForm:{
                id:'',
                num:0,
                pNum:0,
                pName:'',
                pCode:'',
            },
            //入库表单
            outputForm:{
                id:'',
                num:0,
                pNum:0,
                pName:'',
                pCode:'',
            },
            loading:false,
            tableDate:[],
            totalDate:[],
            tableDate_invenChange:[],
            totalDate1:[],
            //新建弹窗
            loading1:false,
            //入库弹窗
            loading2:false,
            //出库弹窗
            loading3:false,
            //库存记录
            loading4:false,
            //库存记录
            loading5:false,

        }
    },
    created() {
        this.onSubmit()
    },

    filters: {
        //匹配当前接口状态
        macthStatue: function (value) {
            if (value === 0){
                return '正常'
            }else if(value ===1){
                return '异常一'
            }else if(value ===2){
                return '异常二'
            }else if(value ===3){
                return '异常三'
            }
        }
    },


    methods:{

       
        //匹配当前接口状态
        macthStatue: function (value) {
            if (value === 0){
                return '正常'
            }else if(value ===1){
                return '异常一'
            }else if(value ===2){
                return '异常二'
            }else if(value ===3){
                return '异常三'
            }
        },

        //修改接口状态
        changeStatue (row){
            this.loading2=true
            //绑定数据
            this.statueForm.id = row.id
            this.statueForm.status = ''
            this.statueForm.crrustatus = this.macthStatue(row.status)
        },

        // 修改页容量
        handleSizeChange (){

        },

        // 主页分页
        handleCurrentChange (currentPage){
            console.log('currentPage='+currentPage)
            console.log('pageSize='+this.pageSize)
            this.tableDate = this.totalDate.slice((currentPage-1)*this.pageSize,currentPage*this.pageSize)
        },
        // 库存详情分页
        handleCurrentChange1 (currentPage1){
            console.log('currentPage1='+currentPage1)
            console.log('pageSize1='+this.pageSize1)
            this.tableDate_invenChange = this.totalDate1.slice((currentPage1-1)*this.pageSize1,currentPage1*this.pageSize1)
        },
        // 提交
        tijiao (){
            //添加时间
            var myDate = new Date()
            var year = myDate.getFullYear();
            var month = myDate.getMonth()+1;
            var day = myDate.getDate();
            var hour = myDate.getHours();
            var min = myDate.getMinutes();
            var s = myDate.getSeconds();

            this.newForm.pUpdate = year+'-'+month+'-'+day+" "+hour+':'+min+':'+s;
            //添加操作人
            this.newForm.pOperator = getCookie('username')
            addInventory(this.newForm).then((res)=>{

                console.log(res)
                if(res.state == 'success') { //新建成功
                    // 清空输入框
                    this.clearNewForm()
                    this.$message.success('新建成功')
                    this.onSubmit()
                    //插入记录
                    const param1 ={
                            pId: '',
                            pName: this.newForm.pName,
                            pCode: this.newForm.pCode,
                            pNum: this.newForm.pNum,
                            pState: '新建',
                            pOperator: this.newForm.pOperator,
                            pDate: this.this.newForm.pUpdate
                        }
                    this.addInvenChange(param1)
                    

                }else{
                    this.$message.warning('新建失败')
                }
            })
        },

        // 新建清空输入框
        clearNewForm () {
            this.newForm.nameChina = ''
            this.newForm.nameEnglish = ''
            this.newForm.path = ''
            this.newForm.returnCode = ''
            this.newForm.error1 = ''
            this.newForm.error2 = ''
            this.newForm.error3 = ''
            this.newForm.createDate = ''
        },

        //查看记录
        detail(row){
            queryInvenChange(row).then((res)=>{

                if(res.state == 'success') { //查询成功
                    //弹出弹窗显示结果
                    this.loading5 = true
                    this.totalDate1 = res.result
                    
                    this.total1 = this.totalDate1.length
                    this.tableDate_invenChange = this.totalDate1.slice((this.currentPage1-1)*this.pageSize1,this.currentPage1*this.pageSize1)
                }else{
                    this.$message.warning('查询失败')
                }
            })
        },

        //修改接口
        updateAna(row){
            this.loading3 = true
            //显示当前库存
            this.updateForm.id = row.id
            this.updateForm.nameChina = row.nameChina
            this.updateForm.nameEnglish = row.nameEnglish
            this.updateForm.path = row.path
            this.updateForm.returnCode = row.returnCode
            this.updateForm.error1 = row.error1
            this.updateForm.error2 = row.error2
            this.updateForm.error3 = row.error3
        },
        //更新库存
        onSubmitOutputInven(){

            var myDate = new Date()
            var year = myDate.getFullYear();
            var month = myDate.getMonth()+1;
            var day = myDate.getDate();
            var hour = myDate.getHours();
            var min = myDate.getMinutes();
            var s = myDate.getSeconds();
            //console.log('出库人名='+getCookie('username'))
            this.updateForm.updateDate = year+'-'+month+'-'+day
            updateAnalog(this.updateForm).then((res)=>{
                console.log(res)
                if(res.state == 'success') { //新建成功
                    this.loading3 = false
                    this.$message.success('更新成功')
                    this.onSubmit()
                    //插入记录
                    // const param1 ={
                    //     pId: this.outputForm.id,
                    //     pName: this.outputForm.pName,
                    //     pCode: this.outputForm.pCode,
                    //     pNum: pNum,
                    //     pState: '出库',
                    //     pDate: param.pUpdate,
                    //     pOperator: param.pOperator
                    // }
                    // this.addInvenChange(param1)
                }else{
                    this.$message.warning('更新失败')
                }
            })

               
        
        },

        
        //更新接口状态
        onSubmitinputInven(){
            
                //添加时间
                var myDate = new Date()
                var year = myDate.getFullYear();
                var month = myDate.getMonth()+1;
                var day = myDate.getDate();
                var hour = myDate.getHours();
                var min = myDate.getMinutes();
                var s = myDate.getSeconds();

                var num = new Number(this.inputForm.num)
                var pNum = new Number(this.inputForm.pNum)
                const param ={
                    id:this.statueForm.id,
                    status:this.statueForm.status,
                    updateDate:year+'-'+month+'-'+day
                }
                updateAnalog(param).then((res)=>{
                    //console.log(res)
                    if(res.state == 'success') { //新建成功
                        this.loading2 = false
                        this.$message.success('更新接口状态成功')
                        this.onSubmit()
                        //插入记录
                        // const param1 ={
                        //         pId: this.inputForm.id,
                        //         pName: this.inputForm.pName,
                        //         pCode: this.inputForm.pCode,
                        //         pNum: pNum,
                        //         pState: '入库',
                        //         pDate: param.pUpdate,
                        //         pOperator: param.pOperator
                        //     }
                        // this.addInvenChange(param1)
                    }else{
                        this.$message.warning('更新接口状态失败')
                    }
                })
        },

        //打开新建商品 弹窗
        newInventory(){
            //打开新建弹窗
            this.loading1 = true
        },
        //新建商品 保存
        onSubmitnewInven(){

            //添加时间
            var myDate = new Date()
            var year = myDate.getFullYear();
            var month = myDate.getMonth()+1;
            var day = myDate.getDate();
            var hour = myDate.getHours();
            var min = myDate.getMinutes();
            var s = myDate.getSeconds();

            this.newForm.createDate = year+'-'+month+'-'+day
            //添加操作人
            //this.newForm.pOperator = getCookie('username')
            addAnalog(this.newForm).then((res)=>{

                console.log(res)
                if(res.state == 'success') { //新建成功
                    this.loading1 = false
                    this.$message.success('新建成功')
                    this.onSubmit()
                    //插入记录
                    // const param1 ={
                    //         pId: '',
                    //         pName: this.newForm.pName,
                    //         pCode: this.newForm.pCode,
                    //         pNum: this.newForm.pNum,
                    //         pState: '新建',
                    //         pOperator: this.newForm.pOperator,
                    //         pDate: this.this.newForm.pUpdate
                    //     }
                    // this.addInvenChange(param1)
                    this.clearNewForm()

                }else{
                    this.$message.warning('新建失败')
                }
            })
        },

        //查询按钮
        onSubmit(){
            
            queryAnalog(this.form).then((res)=>{
                console.log(res)
                console.log(res.state)
                if(res.status == 'S'){
                    console.log('查询正常')
                    this.totalDate = res.list
                    this.total = this.totalDate.length
                    this.tableDate = this.totalDate.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize)
                }else{
                    console.log('查询失败')
                    this.$message.warning("查询失败")
                    
                }
            })


        },

        confirm(data){
            console.log(data)
            confirmButton(data).then((res)=>{
                console.log('res='+res)
                if(res == 'success'){
                    
                    selectAll().then((res)=>{

                        console.log(res)
                        this.tableDate = res
                    })
                }
            })
        },

        //插入到变动记录表中
        addInvenChange(param){

            addInvenChange(param).then((res)=>{
                console.log(res)
                console.log(res.state)
                if(res.state == 'success'){
                    console.log('记录正常')
                    //this.tableDate = res.result
                }else{
                    console.log('记录失败')
                    //this.$message.warning("查询失败")
                    
                }
            })
        }

    }


}


</script>

<style lang="">

    .myDiv .el-form--inline .el-form-item {
        display: block;
        /* margin-right: 10px;
        vertical-align: top; */
    }
</style>




